/* Move down content because we have a fixed navbar that is 50px tall */
body {
    padding-top: 50px;
    padding-bottom: 20px;
    color: #555;
  }
  .btn-danger,
  .bg-danger{
      background-color: #ea6c91 !important;
  }
  
  .btn, input.form-control, select.form-control{
      border-radius: 1px;    
  }
  
  .fixed-top.home{
      min-height: 81px;
      background: #fff;
  }
  
  .form-inline a{
      color: #888;
  }
  .form-inline .form-bottom{
      margin-top: 5px;
      padding-left: 3px;
      font-size: 0.71em;
      color: #888;
  }
  .jumbotron .media h1{
      color: #ea6c91;
      font-size: 40px;
  }
  .jumbotron .media img{
      max-width: 300px;
      border-radius: 10px;
  }
  .modal-content{
      border-radius: 1px;
  }
  footer{
      position: fixed; bottom:0px;padding:3px 20px;background:#ea6c91;width:100%;text-align: center;
      color:#fff; font-weight: bold;
  }
  .error {
      border: 1px solid red !important;
  }
  @media (max-width: 767px){
      .jumbotron .media-object{ margin: 15px auto; }
      .jumbotron .media-left{ display: block; }
      .jumbotron .media-body{ width: 100%;}
  
      #navbar{margin-top: 30px;    }
      #navbar .navbar-form{margin-top:0px; margin-bottom: 0px;}
  }
  @media (min-width: 1200px){
  .jumbotron .media-left {
      padding-right: 40px;
  }    
  .jumbotron .pull-right{
      margin-right: 25px;
  }
  }
  
  /*Accounts page styling */
  .panel-section .table.table-striped{ margin-bottom: 0px; }
  .table .Title{
      color: #ea6c91;
      font-size: 1.1em;
      text-transform: uppercase;
      border-bottom: 2px solid;
  }
  .table .Section, .table .Total{
      background: #f0f0f0;    
  }
  .table .Header .Word,
  .table .Header .Number{
      font-weight: bold;
  }
  
  .sidebar-card .card-img-top{
      font-size: 10em;
      text-align: center;
      width: 100%;
      background: #ddd;
  }
  .sidebar-card .list-group-item:first-child,
  .sidebar-card .list-group-item:last-child{
      border-radius: 0 0 0 0 !important;
  }
  .bnk-panels .panel-section{
      display:none;
  }
  .bnk-panels .panel-section.active{
      display:block;
  }
  /* Alert message styling */
  #togglesim{
      cursor: pointer;
      font-size: 0.9em;
      color: #999;
      text-transform: uppercase;
      float: right;    
  }
  #togglesim .fa-caret-up{
      display:none;
  }
  #togglesim.open .fa-caret-down{
      display:none;
  }
  #togglesim.open .fa-caret-up{
      display:inline-block;
  }
  /* snackbar styling */
  .snackbar-container{transition:all .5s ease;transition-property:top,right,bottom,left,opacity;font-family:Roboto,sans-serif;font-size:14px;min-height:14px;background-color:#070b0e;position:fixed;display:flex;justify-content:space-between;align-items:center;color:#fff;line-height:22px;padding:18px 24px;bottom:0;top:0;opacity:0;z-index:9999}.snackbar-container .action{background:inherit;display:inline-block;border:none;font-size:inherit;text-transform:uppercase;color:#4caf50;margin:0 0 0 24px;padding:0;min-width:min-content;cursor:pointer}@media (min-width:640px){.snackbar-container{min-width:288px;max-width:568px;display:inline-flex;border-radius:2px;margin:24px;bottom:-100px}}@media (max-width:640px){.snackbar-container{left:0;right:0}}.snackbar-pos.bottom-center{top:auto!important;bottom:0;left:50%;transform:translate(-50%,-50%)}.snackbar-pos.bottom-left{top:auto!important;bottom:0;left:0}.snackbar-pos.bottom-right{top:auto!important;bottom:0;right:0}.snackbar-pos.top-left{bottom:auto!important;top:0;left:0}.snackbar-pos.top-center{bottom:auto!important;top:0;left:50%;transform:translate(-50%,-50%)}.snackbar-pos.top-right{bottom:auto!important;top:0;right:0}
  
  
